:root {
    --text-dark: #000;
    --text-light: #fff;
    --text-neutral: #272733;
    --text-light-grey: #bbb;
    --color-blue: #3E9CDC;
    --color-border: #6F6D7B;
    --color-footer: #3D4050;
    --main-bc: #10101D;
    --main-bc-header: rgba(16, 16, 29, 0.90);
}

.icon {
    /* use !important to prevent issues with browser extensions that change fonts */
    font-family: 'loremIpsumnet' !important;
    speak: never;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;

    /* Better Font Rendering =========== */
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

