* {
    box-sizing: border-box;
    font-size: 16px;
    font-family: 'Lato', sans-serif;
    line-height: 1.2;
}

a {
    text-decoration: none;
    color: inherit;
    font-size: inherit;
}

ul {
    list-style: none;
    padding: 0;
    margin: 0;
}

li {
    padding: 0;
    margin: 0;
}

h1,
h2,
h3 {
    margin: 0;
}

p {
    margin: 0;
    padding: 0;
}

button:disabled {
    opacity: .55;
}

.unselectable {
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

input[type=text] {
    &:active {
        outline: 0;
        outline-offset: 0;
    }

    &:hover {
        outline: 0;
        outline-offset: 0;
    }

    &:focus {
        outline: 0;
        outline-offset: 0;
    }

    border: none;
}

input[type=email] {
    &:active {
        outline: 0;
        outline-offset: 0;
    }

    &:hover {
        outline: 0;
        outline-offset: 0;
    }

    &:focus {
        outline: 0;
        outline-offset: 0;
    }

    border: none;
}

input {
    &:focus {
        &::placeholder {
            opacity: 0;
        }

        &::-webkit-input-placeholder {
            color: transparent;
        }

        &::-moz-placeholder {
            color: transparent;
        }

        &:-moz-placeholder {
            color: transparent;
        }

        &:-ms-input-placeholder {
            color: transparent;
        }
    }

    &:disabled {
        opacity: .55;
    }
}

input[type=file] {
    position: absolute;
    opacity: 0;
    pointer-events: none;
}

select {
    width: 100%;
    height: 100%;
    background: transparent;
    border: 0; 
    
    &:active {
        outline: 0;
        outline-offset: 0;
    }

    &:hover {
        outline: 0;
        outline-offset: 0;
    }

    &:focus {
        outline: 0;
        outline-offset: 0;
    }
}

body {
    padding: 0;
    margin: 0;
    counter-reset: section;
}

.wrap {
    width: 100%;
    max-width: 1200px;
    min-width: 320px;
    padding: 0 17.5px;
    margin: 0 auto;
}

.main-cont {
    position: relative;
    overflow-x: hidden;
}