.color-blue {
    color: var(--color-blue);
    font-size: inherit;
}

.btn {
    border-radius: 4px;
    border: 1px solid;
    min-width: 183px;
    padding: 15px 10px;
    text-transform: uppercase;
    text-align: center;
    cursor: pointer;

    &_color-blue {
        color: var(--text-light);
        background-color: var(--color-blue);
        border-color: var(--text-light);

        &:hover {
            background-color: #388fc9;
        }
    }

    &_color-white {
        color: var(--color-blue);
        background-color: var(--text-light);
        border-color: var(--color-border);

        &:hover {
            background-color: #f2f2f2;
        }
    }

    &_width {
        width: 370px;
    }
}

.header {
    width: 100%;
    background-color: var(--main-bc-header);
    color: var(--text-light);
    position: sticky;
    padding: 32px 0;
    z-index: 1;
    top: 0;

    .wrap {
        display: flex;
        align-items: center;
        justify-content: space-between;
        flex-wrap: wrap;
    }

    &__name {
        display: flex;
        justify-content: left;
        font-size: 28px;
        font-weight: 800;
        text-transform: uppercase;
    }

    &__navbar {
        display: flex;
        justify-content: right;
        column-gap: 60px;

    }

    &__a {
        text-transform: uppercase;
        font-size: 13px;
        font-weight: 600;

        &:hover {
            color: var(--color-blue);
        }
    }
}

.cont {
    &.cont_bc-img-1 {
        background-image: url(../bc-img-1.webp);
        background-repeat: no-repeat;
        background-size: cover;
        height: 75vh;
        display: flex;
        align-items: center;
        background-position: 65%;
        background-color: var(--text-light);
    }

    &.cont_bc-img-2 {
        background-image: url(../bc-img-2.webp);
        background-repeat: no-repeat;
        background-size: cover;
        background-color: var(--main-bc);
        display: flex;
        align-items: center;
        height: auto;
        padding: 121px 0 150px 0;
    }

    .wrap {
        height: 50%;
    }

    .block-1 {
        color: var(--text-dark);
        width: 65%;
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: space-between;

        &__title {
            font-size: calc(1rem + 1.7vw);
            font-weight: 700;
            text-transform: uppercase;
        }

        &__desc {
            &-title {
                font-size: 20px;
                font-weight: 700;
                margin-bottom: 10px;
                display: block;
            }
        }

        &__ul {
            font-size: 20px;
            font-weight: 400;
            list-style-position: inside;
        }

        &__li {
            &::before {
                content: '\2010';
                margin-left: 10px;
                margin-right: 10px;
            }
        }

        &__btns {
            width: 395px;
            display: flex;
            justify-content: space-between;
        }
    }

    .block-2 {
        display: flex;
        flex-direction: column;
        align-items: center;
        color: var(--text-light);

        &__title {
            font-size: 36px;
            font-weight: 700;
            text-transform: uppercase;
        }

        &__subtitle {
            margin: 30px 0 80px 0;
            color: var(--text-light-grey);
        }

        .row-elements {
            display: flex;
            width: 100%;
            justify-content: space-between;
            flex-wrap: wrap;
            align-items: center;


            &__inner {
                display: flex;
                flex-direction: column;
                align-items: center;
                width: 209px;

                &:last-child {
                    .row-elements__element::after {
                        display: none;
                    }

                }
            }

            &__element {
                background-color: var(--color-blue);
                border-radius: 50%;
                border: 5px solid #286690;
                width: 100px;
                height: 100px;
                display: flex;
                justify-content: center;
                align-items: center;
                margin-bottom: 20px;
                position: relative;

                &::after {
                    content: "";
                    display: inline-block;
                    background-image: url(data:image/png;base64,);
                    position: absolute;
                    width: 110px;
                    height: 6px;
                    background-repeat: no-repeat;
                    position: absolute;
                    top: 40px;
                    left: 110px;
                }

            }

            &__icon {
                font-size: 50px;
            }
        }

        .form {
            width: 100%;
            display: flex;
            flex-direction: column;
            align-items: center;
            margin-top: 80px;

            &__row {
                display: flex;
                flex-direction: row;
                justify-content: space-between;
                height: 48px;
                width: 100%;
                margin-bottom: 30px;
            }

            &__select {
                position: relative;
                width: 32%;

                .select-wrapper {
                    position: relative;
                    height: 100%;

                    &__custom {
                        position: relative;
                        height: 100%;
                        width: 100%;
                    }

                    &__trigger {
                        border-radius: 3px;
                        border: 1px solid var(--text-light);
                        background: rgba(255, 255, 255, 0.85);
                        color: var(--text-neutral);
                        font-size: 18px;
                        font-weight: 400;
                        cursor: pointer;
                        position: relative;
                        width: 100%;
                        height: 100%;
                        padding: 10px;

                        &::after {
                            content: url(data:image/svg+xml;base64,);
                            position: absolute;
                            top: 10px;
                            right: 10px;
                        }
                    }

                    &__options {
                        position: absolute;
                        top: 48px;
                        left: 0;
                        width: 100%;
                        border: 1px solid var(--color-footer);
                        background-color: rgba(39, 39, 51, 0.90);
                        z-index: 1;
                        padding: 10px;
                        display: none;
                    }

                    &__isActive {
                        .select-wrapper__options {
                            display: block;
                        }

                        .select-wrapper__trigger {
                            border-radius: 3px 3px 0px 0px;
                            background: rgba(61, 64, 80, 0.85);
                            color: var(--text-light);
                            border: 1px solid var(--color-footer);

                            &::after {
                                content: url(data:image/svg+xml;base64,);
                                position: absolute;
                                top: 10px;
                                right: 10px;
                            }
                        }
                    }

                    &__option {
                        position: relative;
                        padding: 0.8rem;
                        cursor: pointer;
                    }
                }
            }

            &__input {
                width: 32%;
                height: 100%;

                input {
                    height: 100%;
                }

                input[type=email],
                input[type=text] {
                    width: 100%;
                    border-radius: 3px;
                    border: 1px solid var(--text-light);
                    background: rgba(255, 255, 255, 0.85);
                    color: var(--text-neutral);
                    font-size: 18px;
                    font-weight: 400;
                    padding: 10px;
                }
            }

            &__range {
                width: 66%;
                height: 110%;
                display: flex;
                flex-direction: column;
                justify-content: space-between;

                input[type=range] {
                    width: 100%;
                    appearance: none;
                    background: rgba(255, 255, 255, 0.70);
                    height: 6px;
                    border-radius: 3px;
                    border: 1px solid var(--text-light);

                    &::-webkit-slider-thumb {
                        -webkit-appearance: none;
                        /* Убираем стандартные стили вебкит браузеров */
                        appearance: none;
                        width: 21px;
                        height: 21px;
                        background: #42A9ED;
                        border: 3px solid #286690;
                        border-radius: 50%;
                        cursor: pointer;
                    }
                }

                &-label {
                    color: var(--text-light);
                    font-size: 18px;
                    font-weight: 400;
                    display: flex;
                    flex-direction: row;
                    align-items: center;
                    justify-content: space-between;

                    .range-value {
                        font-size: 18px;
                        font-weight: 600;
                    }
                }
            }

            &__file {
                width: 32%;
                position: relative;
                text-align: center;
                border-radius: 3px;
                border: 2px solid var(--color-blue);
                background: var(--text-light);
                display: flex;
                align-items: center;
                justify-content: center;
                cursor: pointer;

                &-icon {
                    height: 20px;
                    margin-right: 10px;
                }

                &-text {
                    color: var(--text-neutral);
                    font-size: 15px;
                    font-weight: 600;
                    text-transform: uppercase;
                }

                &-label {
                    display: flex;
                    align-items: center;
                    cursor: pointer;
                }

                &:hover {
                    background-color: #f2f2f2;
                }
            }

            .btn {
                margin-top: 15px;
            }
        }
    }
}

footer {
    background: var(--color-footer);
    padding: 30px 0;
    color: var(--text-light);

    .footer {
        display: flex;
        flex-wrap: wrap;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
        flex-wrap: wrap;
        height: auto;

        &__inner {
            display: flex;
            flex-direction: row;
            min-width: 330px;
            justify-content: space-between;
            align-items: center;
            flex-wrap: wrap;

            &_center {
                min-width: 485px;
                border-left: 1px solid #C3C3C3;
                border-right: 1px solid #C3C3C3;
                padding: 0 30px;
            }

            &_pad_right {
                padding-right: 30px;
            }

            &_pad_left {
                padding-left: 20px;
            }
        }

        &__text {
            font-size: 13px;
        }

        &__icon-text {
            display: flex;
            align-items: center;

            &>span,
            &>a {
                display: block;
            }

            &>a {
                &:hover {
                    color: var(--text-light-grey);
                }
            }

            .icon {
                font-size: 20px;
                color: var(--color-blue);
                padding-right: 7px;
            }
        }
    }
}

@media screen and (max-width: 1200px) {

    .header {
        width: 100%;
    }

    .cont {

        .block-2 {
            .row-elements {
                justify-content: center;

                &__inner {
                    margin-bottom: 25px;
                }

                &__element {
                    &::after {
                        content: "";
                        display: none;
                    }
                }
            }

            .form__select {
                .select-wrapper__trigger {
                    font-size: 16px;
                }
            }
        }
    }

    footer {
        .footer {
            justify-content: center;
            gap: 20px;

            &__inner_center {
                border: none;
            }
        }
    }
}

@media screen and (max-width: 845px) {

    .header {
        &__name {
            justify-content: center;
            width: 100%;
            margin-bottom: 20px;
        }

        &__navbar {
            justify-content: center;
            column-gap: 35px;
            width: 100%;
        }
    }

    .cont {
        &.cont_bc-img-1 {
            height: 70vh;
        }

        .block-2 {
            .row-elements {
                justify-content: center;

                &__inner {
                    margin-bottom: 25px;
                }

                &__element {
                    &::after {
                        content: "";
                        display: none;
                    }
                }
            }

            .form__select {
                .select-wrapper__trigger {
                    font-size: 14px;
                }
            }
        }
    }

    footer {
        .footer {
            justify-content: center;
            gap: 20px;
        }
    }
}

@media screen and (max-width: 440px) {

    .header {
        &__navbar {
            column-gap: 25px;
        }

        &__a {
            font-size: 10px;
        }
    }

    .cont {

        &.cont_bc-img-1 {

            .wrap {
                margin-top: 0;
                height: 75%;
            }
        }

        &.cont_bc-img-2 {
            padding: 80px 0;
        }

        .wrap {
            height: 55%;
        }

        .block-1__btns {
            margin-top: 20px;
            width: 280px;

            .btn {
                min-width: 135px;
            }
        }

        .block-2 {

            &__title,
            &__subtitle {
                text-align: center;
            }

            .form {
                &__row {
                    flex-direction: column;
                    gap: 20px;
                    height: auto;
                    margin-bottom: 55px;
                }

                &__select,
                &__input,
                &__range {
                    width: 100%;
                }

                &__range {
                    height: 50px;

                    &-label {
                        font-size: 14px;

                        .range-value {
                            font-size: 14px;
                        }
                    }
                }

                &__file {
                    width: 100%;
                    padding: 10px 0;
                }

                .btn_width {
                    width: 290px;
                }
            }
        }

    }

    footer {
        .footer {
            &__inner {
                min-width: 300px;
                justify-content: space-evenly;
                padding: 0;

                &_center {
                    min-width: 300px;
                }
            }

            &__icon-text {
                &>a {
                    font-size: 12px;
                }
            }
        }
    }
}